@import '../../styles/variables';
@import '../../styles/mixins';
@import '../../styles/fonts';

.text {
	position: relative;
	margin-top: -3rem;
	margin-bottom: 17.176rem;
	color: $white;
	font-family: 'Georgia';
	font-style: normal;
	font-weight: 700;
	font-size: 6.5rem;
	line-height: 6.5rem;

	@include mobile-only {
		display: block;
		width: 25.6rem;
		margin-top: -1rem;
		margin-left: auto;
		margin-right: auto;
		padding: 0;
		margin-bottom: 6.4rem;
		text-align: center;
		font-size: 2.8rem;
		line-height: 3.2rem;
	}
}

.firstHeart {
	position: absolute;
	top: -2.8rem;
	left: 31.4rem;
	width: 23.104rem;
	height: 21.351rem;
	transform: rotate(15deg);

	@include mobile-only {
		top: 0.723rem;
		left: 5.3rem;
		width: 11.295rem;
		height: 10.438rem;
	}
}

.nameApp {
	margin-top: 10rem;
	margin-left: 12rem;

	@include mobile-only {
		margin-top: 9rem;
		display: block;
		margin-left: auto;
		margin-right: auto;
		text-align: center;
	}
}

.star {
	position: absolute;
	top: 31.435rem;
	left: 115.4rem;
	width: 33.5rem;
	height: 33.5rem;

	@include mobile-only {
		top: 20.9rem;
		left: 28rem;
		width: 16.378rem;
		height: 16.378rem;
	}
}

.logo {
	margin: 0;
	padding: 0;
	margin-left: -3rem;
	color: #0291E0;
	font-size: 38rem;
	font-family: 'Dangrek';
	line-height: 38rem;
	letter-spacing: -0.532rem;

	@include mobile-only {
		margin-left: 0rem;
		font-size: 10.2rem;
		line-height: 12.8rem;
		letter-spacing: -0.1428rem;
	}
}

.secondHeart {
	position: absolute;
	top: 60.9rem;
	left: 57.6rem;
	width: 18.939rem;
	height: 17.502rem;
	transform: rotate(45deg);

	@include mobile-only {
		display: none;
	}
}

.thirdHeart {
	position: absolute;
	top: 74.1rem;
	left: 9.9rem;
	width: 18.939rem;
	height: 17.502rem;

	@include mobile-only {
		top: 53rem;
		left: 0.1rem;
		width: 13.136rem;
		height: 12.139rem;
	}
}

.appGallery {
	width: 22rem;
	height: 6.364rem;
	margin-left: 0.7rem;
	margin-right: 0.7rem;

	@include mobile-only {
		width: 22rem;
		height: 6.4rem;
		margin-top: 1.436rem;
	}
}

.today {
	width: 73rem;
	margin-top: 0rem;
	margin-bottom: 0rem;
	text-align: end;
	font-family: 'Montserrat';
	font-style: normal;
	font-weight: 600;
	font-size: 7.6rem;
	line-height: 7.6rem;
	color: $white;

	@include mobile-only {
		width: 30rem;
		margin-top: 0rem;
		padding-top: 11.7rem;
		margin-left: 4.7rem;
		margin-bottom: 34rem;
		text-align: center;
		font-size: 5.2rem;
		line-height: 5.4rem;
		color: $white;
	}
}

.phone {
	position: absolute;
	top: 57.8rem;
	right: 18rem;
	height: 73.554rem;
	transform: rotate(-10deg);
	z-index: 1;

	@include mobile-only {
		top: 89.6rem;
		left: 0;
		right: 0;
		width: 25.058rem;
		height: 51.142rem;
		margin-left: auto;
		margin-right: auto;
		text-align: center;
		z-index: 1;
	}
}

.swipeContainer {
	width: 144rem;
	height: 135.4rem;
	background-image: url(../../assets/svg/ellipse.svg);
	background-size: cover;
	z-index: 0;

	@include mobile-only {
		width: 37.5rem;
		height: 146.7rem;
		margin-left: auto;
		margin-right: auto;
		text-align: center;
		background-position: center;
	}
}

.publishWishes {
	width: 62.5rem;
	padding-top: 8.9rem;
	margin-left: 20.9rem;
	margin-bottom: 9.2rem;
	font-family: 'Montserrat';
	font-style: normal;
	font-weight: 600;
	font-size: 6rem;
	text-align: end;
	line-height: 6.4rem;
	color: $main-black;

	@include mobile-only {
		width: 35.5rem;
		padding-top: 24.2rem;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 34.8rem;
		text-align: center;
		font-size: 3.6rem;
		line-height: 4rem;
	}
}

.round {
	width: 58.9rem;
	margin: auto;
	text-align: center;
	color: $white;
	font-family: 'Montserrat';
	font-style: normal;
	font-weight: 600;
	font-size: 5.4rem;
	line-height: 6rem;
	letter-spacing: -0.1rem;

	@include mobile-only {
		width: 34.7rem;
		margin-top: 33rem;
		font-family: 'Montserrat';
		font-style: normal;
		font-weight: 600;
		font-size: 3.6rem;
		line-height: 4rem;
		letter-spacing: -0.1rem;
	}
}

.circle {
	display: flex;
	background-image: url(../../assets/svg/blackCircle.svg);
	background-size: contain;
	width: 82.8rem;
	height: 82.8rem;
	z-index: 0;

	@include mobile-only {
		position: relative;
		top: -53rem;
		right: 12rem;
		width: 62.3rem;
		height: 62.3rem;
	}
}

.swipePhone {
	height: 71rem;
	@include mobile-only {
		height: 58.933rem;
		position: relative;
		top: -24rem;
		z-index: 1;
	}
}

.swipeBlock {
	display: flex;
	justify-content: center;
	@include mobile-only {
		display: block;
	}
}

.phoneContainer {
	position: relative;
	left: 9rem;
	margin: auto 0;
	height: 71rem;
	z-index: 1;

	@include mobile-only {
		left: 0;
		right: 0;
		margin-left: auto;
		margin-right: auto;
		height: 58.933rem;
		text-align: center;
		justify-content: center;
		display: flex;
	}
}

.cardContainer {
	position: absolute;
	top: 9.8rem;
	left: 1.5rem;
	overflow: hidden;
	z-index: 2;

	@include mobile-only {
		left: 5.5rem;
		top: -16rem;
	}
}

.card {
	height: 51.8rem;

	@include mobile-only {
		height: 43.2rem;
	}
}

.circleIcon {
	position: absolute;
	top: 40%;
	width: 9.56rem;
	height: 9.56rem;
	display: flex;
	justify-content: center;
	z-index: 3;
	background-color: $iconBackground;
	border-radius: 6.5rem;
	border: 0.4rem solid #ffffff;

	@include mobile-only {
		width: 8.56rem;
		height: 8.56rem;
	}
}

.likeIcon {
	width: 3rem;
}

.cancelIcon {
	width: 2.4rem;
}

.cancelBlock {
	left: 2rem;
}

.likeBlock {
	right: 2rem;
}
