@import '../../styles/mixins';

.stores {
	text-align: center;
	margin-top: 13rem;

	@include mobile-only {
		margin-top: 16.4rem;
	}
}

.title {
	margin-bottom: 4.45rem;
	font-family: 'SFProDisplay-Semibold';
	font-style: normal;
	font-weight: 600;
	font-size: 5.6rem;
	line-height: 100%;
	letter-spacing: -0.1rem;
	color: #ffffff;

	@include mobile-only {
		font-size: 3.6rem;
	}
}

.storeIcons {
	height: 8.95rem;
	width: 31rem;
	margin-left: 0.35rem;
	margin-right: 0.35rem;

	@include mobile-only {
		height: 7.2rem;
		width: 24.75rem;
		margin-top: 1.4rem;
	}
}
