@import '../../styles/variables';
@import '../../styles/mixins';

.advantages {
	position: relative;
	text-align: center;
}

.love {
	position: absolute;
	top: 6.4rem;
	left: 25.9rem;
	display: flex;
	width: 39.3rem;
	height: 39.3rem;
	justify-content: center;
	align-items: center;
	font-family: 'SFProDisplay-Semibold';
	font-style: normal;
	font-weight: 600;
	font-size: 6.4rem;
	line-height: 4.4rem;
	color: $white;
	background: $red;
	border-radius: 500rem;
	z-index: 0;

	@include mobile-only {
		top: 9.8rem;
		left: 2rem;
		display: flex;
		width: 29.9rem;
		height: 29.9rem;
		font-size: 5.8rem;
		transform: none !important;
	}
}

