	font-weight: 600;
	font-size: 6.4rem;
	line-height: 4.4rem;
	background: $yellow;
	border-radius: 500rem;
	color: $white;
	z-index: 0;

	@include mobile-only {
		top: 41.1rem;
		left: 11.3rem;
		display: flex;
		width: 24.2rem;
		height: 24.2rem;
