	font-family: 'SFProDisplay-Semibold';
	font-style: normal;
	font-weight: 600;
	font-size: 5.6rem;
	line-height: 100%;
	letter-spacing: -0.1rem;
	color: $white;

	@include mobile-only {
		width: 33.5rem;
		padding-top: 1.6rem;
		margin-left: 2rem;
		padding-bottom: 26.2rem;
		font-weight: 600;
		font-size: 3.2rem;
		letter-spacing: -0.1rem;
		text-align: center;
	}
}

