@import '../../styles/variables';
@import '../../styles/mixins';

.container {
	text-align: center;
}

.title {
	margin-top: 0rem;
	font-family: 'SFProDisplay-Semibold';
	font-style: normal;
	font-weight: 600;
	font-size: 5.6rem;
	line-height: 100%;
	letter-spacing: -0.1rem;
	color: $white;

	@include mobile-only {
		font-family: 'Montserrat';
		font-size: 3.6rem;
		width: 33.5rem;
		margin-top: 0;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 0;
	}
}

.slide {
	position: relative;
  	overflow: hidden;
	width: 31.2rem !important;
	height: 61.7rem;
	border-radius: 1.6rem;
	background-color: black;
}

.image {
  position: absolute;
	width: 31.2rem;
	z-index: 0;
}

.slider {
	width: 103.6rem;
	margin-top: 4.4rem;
	margin-left: auto;
	margin-right: auto;
	@include mobile-only {
		width: 37.5rem;
		margin-top: 2rem;
	}
}

.slideDescription {
	position: absolute;
	bottom: 2.4rem;
  z-index: 1;
	text-align: start;
	margin-left: 2.4rem;
	margin-right: 2.4rem;
}

.slideTitle {
	font-family: 'SFProDisplay-Semibold';
	font-style: normal;
	font-weight: 600;
	font-size: 2.6rem;
	line-height: 2.8rem;
	letter-spacing: -0.1rem;
	color: $white;
}

.slideText {
	margin-top: 1.2rem;
	font-family: 'SFProDisplay-Medium';
	font-style: normal;
	font-weight: 500;
	font-size: 1.8rem;
	line-height: 2.2rem;
	letter-spacing: -0.1rem;
	color: $white;
}
