@import './variables';
@import './fonts';
@import './mixins';

:root {
	color-scheme: dark;
	scrollbar-color: #000;
}

html {
	font-size: calc(100vw / 1440 * 10);
	color-scheme: dark;

	@include mobile-only {
		font-size: calc(100vw / 375 * 10);
	}
}

body {
	margin: 0;
	width: 100% !important;
	background-color: $main-black;
	color-scheme: dark;
	scrollbar-color: #000;
}

.app {
	position: relative;
	overflow: hidden;
}
