@import '../../styles/variables';
@import '../../styles/mixins';

.footer {
	justify-content: center;
	margin-top: 10.6rem;
	width: 144rem;
	height: 23.2rem;
	background-image: url(../../assets/svg/footer.svg);
	background-size: cover;

	@include mobile-only {
		display: grid;
		margin-top: 10.6rem;
		width: 37.5rem;
		height: 32rem;
		background-image: url(../../assets/svg/footerMobile.svg);
	}
}

.blackLogo {
	margin-top: 5.8rem;
	margin-bottom: 4.2rem;
	width: 100%;
	height: 8rem;

	@include mobile-only {
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 2.4rem;
		width: 23.747rem;
		height: 5.6rem;
	}
}

.footerNav {
	font-family: 'SFProDisplay-Semibold';
	font-style: normal;
	font-weight: 600;
	font-size: 2.4rem;
	line-height: 3.2rem;
	text-align: center;
	letter-spacing: -0.1rem;
	color: $main-black;

	@include mobile-only {
		margin-top: 1.6rem;
	}
}

a {
	text-decoration: none;
}

.navigation {
	display: inline-flex;
	width: 100%;
	justify-content: space-evenly;

	@include mobile-only {
		display: block;
	}
}
